<template>
    <div>
        <div class="banner" @click="handleBannerClick">
            <img class="banner-img" :src="bannerImg" alt="">
            <div class="banner-info">
                <div class="banner-title">{{ sightName }}</div>
                <div class="banner-number">
                    <span class="iconfont banner-icon">&#xe692;</span>
                    {{ gallaryImgs.length }}
                </div>
            </div>
        </div>
        <common-fade>
            <common-gallary 
                :imgs="gallaryImgs" 
                v-show="showGallary" 
                @close="handleGallaryClose"
            >
            </common-gallary>
        </common-fade>
    </div>
</template>
<script>
import commonGallary from 'common/gallary/gallary'
import commonFade from 'common/fade/fade'
export default {
    name: 'detailBanner',
    props: {
        sightName: String,
        bannerImg: String,
        gallaryImgs: [Array, String]
    },
    components: {
        commonGallary,
        commonFade
    },
    data () {
        return {
            showGallary: false
        }
    },
    methods: {
        handleBannerClick () {
            this.showGallary = true
        },
        handleGallaryClose () {
            this.showGallary = false
        }
    }
}
</script>
<style lang="stylus" scoped>
.banner
    overflow hidden
    position relative
    height 0
    padding-bottom 55%
    .banner-img
        width 100%
    .banner-info
        display flex
        position absolute
        left 0
        right 0
        bottom 0
        line-height .6rem
        color #fff
        background-image linear-gradient(top, rgba(0, 0, 0, 0), rgba(0, 0, 0, .8))
        .banner-title
            flex 1
            font-size .32rem
            padding 0 .2rem
        .banner-number
            margin-top .14rem
            padding 0 .4rem
            line-height .32rem
            height .32rem
            border-radius .2rem
            background rgba(0, 0, 0, .7)
            font-size .24rem
            .banner-icon
                font-size .24rem
</style>





