<template>
	<div class="header">
		<div class="header-left">
			<div class="iconfont back-icon">&#xe624;</div>
		</div>
		<div class="header-input">
			<span class="iconfont">&#xe632;</span>
			输入城市/景点/游玩主题
		</div>
		<router-link :to="{name:'city'}">
			<div class="header-right">
				{{ this.city }}
				<!-- {{ this.doubleCity }} -->
				<!-- {{ this.$store.getters.doubleCity }} -->
				<!-- {{ this.$store.state.city }} -->
				<span class="iconfont arrow-icon">&#xe64a;</span>
			</div>
		</router-link>
	</div>
</template>
<script>
import { mapState, mapGetters } from 'vuex'
export default{
	name: "homeheader",
	computed: {
		...mapState(["city"]),
		...mapGetters(['doubleCity'])
	}
}
</script>
<style lang="stylus" scoped>
@import '~styles/varibles.styl'
.header
	display  flex
	line-height $headerHeight
	height $headerHeight
	background $bgColor
	color #fff
	.header-left
		width .64rem
		float left
		.back-icon
			text-align center
			font-size .4rem
	.header-input
		flex 1
		line-height .64rem
		height .64rem
		margin-top .12rem
		margin-left .2rem
		padding-left .2rem
		background #fff
		border-radius .1rem
		color #ccc
	.header-right
		color #fff
		min-width 1.04rem
		padding 0 0.1rem
		float right
		text-align center
		.arrow-icon
			margin-left -.04rem
			font-size .24rem
</style>


